import React from 'react';
import { useParams, Link } from 'react-router-dom';

export default function WebGame() {
  const { slug } = useParams();
  const src = `/games/${slug}/index.html`;
  return (
    <div className="card" style={{ height: 'calc(100vh - 140px)' }}>
      <div style={{ display: 'flex', gap: 8, justifyContent: 'space-between', alignItems: 'center', marginBottom: 8 }}>
        <div style={{ display: 'flex', gap: 8 }}>
          <Link className="week-btn" to="/games">‚üµ Retour</Link>
          <a className="week-btn" href={src} target="_blank" rel="noopener noreferrer">Ouvrir dans un onglet</a>
        </div>
        <span className="pill pill--info">{slug}</span>
      </div>
      <iframe
        title={slug}
        src={src}
        style={{ width: '100%', height: '100%', border: '1px solid var(--line)', borderRadius: 8, background: '#000' }}
      />
    </div>
  );
}